<template>
    <div>
        <div class="container grid">
            <div class="col-8">
                <h2 class="main-title-widget">{{class_info.name}}</h2>
                <learn-outline-widget @sel_class="sel_class"
                                      :class_info="class_info"></learn-outline-widget>
            </div>
            <div class="col-4 pad-left-2">
                <div class="pad-h-5"></div>
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">{{cur_class.name}}</h3>
                    <hr class="seg-widget"/>
                    <div class="basic-article-widget pad-w-2">
                        <p class="ba-w-title"></p>
                        <p class="ba-w-p" v-for="desc of class_desc">{{desc}}</p>
                    </div>
                    <div class="pcenter">
                        <router-link :to="class_link" class="base-btn-widget"></router-link>
                    </div>
                    <div class="pad-h-2"></div>
                </div>
            </div>
        </div>
        <div class="pad-h-4"></div>
    </div>
</template>

<script>
    import LearnOutline from '@/components/widget/learn-outline-widget';        //
    import ClassManager from '@/lib/ClassManager';

    /**/
    var class_info_data = {//data:
            id: "a",
            name: "",
            list: [
                {
                    title: "",
                    datas: [
                        {
                            id: "a1",
                            name: "",
                            desc: "111Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate."
                        },
                        {
                            id: "a2",
                            name: "",
                            desc: "222Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate."
                        },
                        {
                            id: "a3",
                            name: "",
                            desc: "333Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate."
                        },
                    ]
                },
                {
                    title: "",
                    datas: [
                        {
                            id: "b1",
                            name: "Flume"
                        },
                        {
                            id: "b2",
                            name: "Sqoop"
                        },
                        {
                            id: "b3",
                            name: "Kafka"
                        },
                        {
                            id: "b4",
                            name: "Hive"
                        },
                        {
                            id: "b5",
                            name: "Hbase"
                        },
                        {
                            id: "b6",
                            name: "Storm"
                        },
                        {
                            id: "b7",
                            name: "Oozie"
                        },
                        {
                            id: "b8",
                            name: "Flume"
                        },
                        {
                            id: "b9",
                            name: "CDH"
                        },
                        {
                            id: "b10",
                            name: "SparkRDD"
                        },
                        {
                            id: "b11",
                            name: "SparkSQL"
                        },
                        {
                            id: "b12",
                            name: "SparkStreaming"
                        },
                    ]
                },
                {
                    title: "",
                    datas: [
                        {
                            id: "c1",
                            name: "Linux"
                        },
                        {
                            id: "c2",
                            name: "Hadoop"
                        },
                        {
                            id: "c3",
                            name: "Zookeeper"
                        },
                    ]
                },
                {
                    title: "",
                    datas: [
                        {
                            id: "d1",
                            name: "SQL"
                        },
                        {
                            id: "d2",
                            name: "MySQL"
                        },
                        {
                            id: "d3",
                            name: "Redis"
                        },
                    ]
                },
                {
                    title: "",
                    datas: [
                        {
                            id: "e1",
                            name: "Java"
                        },
                        {
                            id: "e2",
                            name: "Python"
                        },
                        {
                            id: "e3",
                            name: "Scala"
                        },
                        {
                            id: "e4",
                            name: "Shell"
                        },
                    ]
                },
                {
                    title: "",
                    datas: [
                        {
                            id: "f1",
                            name: ""
                        },
                        {
                            id: "f2",
                            name: "HTML"
                        },
                        {
                            id: "f3",
                            name: "CSS"
                        },
                        {
                            id: "f4",
                            name: "Javascript"
                        },
                    ]
                },
            ]
        };

    var user_learn_progress = {//data:
        lasted: "0",
    };

    export default {
        name: "learn_start",
        components: {
            'learn-outline-widget': LearnOutline,
        },
        data() {
            return {

                /**/
                class_info: {},

                /**/
                cur_class: {},

            }
        },
        computed: {

            /**/
            class_desc() {
                var desc = this.cur_class.desc;

                return desc ? desc.split("\n") : [];
            },

            /**/
            class_link() {
                return "/learn_path?cid=" + this.cur_class.id + "&cname=" + this.cur_class.name;
            },

        },
        methods: {

            /**/
            sel_class(datas) {
                this.cur_class = datas;
            },

        },
        mounted() {
            this.$axios.all([
                this.$axios.get("major/" + this.$route.query.class_id),
                this.$axios.get("course/"),
                this.$axios.get("complete/"),
            ]).then(this.$axios.spread((r1, r2, r3) => {
                let cur_class = r1.data,
                    list = [],
                    courses = r2.data;

                /**/
                let progress = r3.data;
                progress.forEach(pro => {
                    let course_id = pro.chapter.course_name.id;
                    if(pro.course_end === true){
                        user_learn_progress[course_id] = 2;//
                    }else{
                        user_learn_progress[course_id] = 1;//
                    }
                    user_learn_progress.lasted = course_id;
                });

                /**/
                cur_class.sub_cat.forEach(cat => {
                    let datas = [];
                    list.push({
                        title: cat.name,
                        datas: datas
                    })

                    /**/
                    for (var i = 0, course, cur_course; i < courses.length; i++) {
                        course = courses[i];
                        if (cat.id === course.category.id) {
                            cur_course = courses.splice(i, 1)[0]["course"];
                            datas.push({
                                id: cur_course.id,
                                name: cur_course.name,
                                desc: cur_course.course_task,
                                cover: cur_course.image,
                            });
                            i--;
                        }
                    }

                })

                /**/
                let class_info_data = {
                        id: cur_class.id,
                        name: cur_class.name,
                        list: list
                    },
                    class_manager = new ClassManager(user_learn_progress, class_info_data);
                this.class_info = class_manager.get_class_outline();
                this.cur_class = class_manager.get_cur_class();
            }))
        },
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/basic-article-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/class-desc-widget.css";
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/copyright-widget.css";
</style>



